<template>
  <v-dialog v-model="show" max-width="65%">
    <v-container class="elevation-24" style="border: 3px  #13314a; border-radius: 20px">
    
    <v-card style="padding: 20px; border: 3px solid #13314a; border-radius: 20px">
      <v-form>
        <v-row justify="center"  style="padding-top: 40px">
          <h1 class="title">EDITAR MOVIMIENTO</h1>
        </v-row>
        <v-row justify="center"  class="mt-10">
          <v-btn
            class="but white--text"
            :color="color2"
            large
            style="margin-right: 15px"
            v-model="pressedI"
            @click="
              pressedI = true;
              pressedG = false;
              movementType = 'Ingreso';
              switchI();
            "
          >
            INGRESO
            <v-icon style="margin-right: -9px">mdi-chevron-up </v-icon>
          </v-btn>
          <v-btn
            class="but white--text"
            :color="color1"
            large
            style="margin-left: 15px"
            v-model="pressedG"
            @click="
              pressedI = false;
              pressedG = true;
              movementType = 'Gasto';
              switchG();
            "
          >
            GASTO
            <v-icon style="margin-right: -9px">mdi-chevron-down </v-icon>
          </v-btn>
        </v-row>
        <v-row justify="center" class="mt-4">
          <v-col md="6" class="text-center">
            <v-text-field
              id="movementConcept"
              v-model="movementConcept"
              label="Concepto"
              name="movementConcept"
              class="customText"
              type="text"
              required
              :error-messages="conceptErrors"
              @input="$v.movementConcept.$touch()"
              @blur="$v.movementConcept.$touch()"
            />
          </v-col>
          <v-col md="6" class="text-center">
            <v-text-field
              id="movementAmount"
              v-model="movementAmount"
              label="Importe (€)"
              name="movementAmount"
              class="customText"
              type="number"
              required
              :error-messages="amountErrors"
              @input="$v.movementAmount.$touch()"
              @blur="$v.movementAmount.$touch()"
            />
          </v-col>
        </v-row>
        <v-row justify="center" align="center">
          <v-col class="text-center" md="6">
            <v-menu
              v-model="menu2"
              :close-on-content-click="false"
              :nudge-right="40"
              transition="scale-transition"
              offset-y
              min-width="290px"
            >
              <template v-slot:activator="{ on, attrs }">
                <v-text-field
                  class="customText"
                  v-model="initialDate"
                  label="Fecha de Inicio"
                  prepend-icon="mdi-calendar"
                  readonly
                  v-bind="attrs"
                  v-on="on"
                ></v-text-field>
              </template>
              <v-date-picker
                v-model="initialDate"
                @input="menu2 = false"
                :landscape="true"
              ></v-date-picker>
            </v-menu>
          </v-col>
          <v-col
            ><v-select
              required
              label="Categoría"
              class="customText"
              v-model="movementCategory"
              :items="categories"
              :error-messages="categoryErrors"
              @input="$v.movementCategory.$touch()"
              @blur="$v.movementCategory.$touch()"
            ></v-select
          ></v-col>
        </v-row>

        <v-row justify="center">
          <v-col md="6" class="d-flex justify-space-around">
            <v-textarea
              class="customText ml-12 mr-12"
              outlined
              name="movementComment"
              label="Comentario"
              rows="2"
              v-model="movementComment"
              no-resize
            ></v-textarea>
          </v-col>
          <v-col md="6" style="padding-top: 0px">
            <v-row justify="center" align="center">
            </v-row>
            <v-row justify="center" align="center" class="mt-2">
              <v-col class="text-center">
                <v-btn
                  class="but white--text"
                  color="primary"
                  style="margin-right: 30px"
                  large
                  @click="cancel"
                  >CANCELAR</v-btn
                >
                <v-btn
                  class="but white--text"
                  color="primary"
                  style="margin-left: 10px"
                  large
                  @click="confirm"
                  >CONFIRMAR</v-btn
                >
              </v-col>
            </v-row>
          </v-col>
        </v-row>
      </v-form>
    </v-card>
    </v-container>
  </v-dialog>
</template>

<style scoped>
.title {
  font-size: 2.5em !important;
  color: #13314a !important;
}

.but {
  font-size: 1em;
}

.customText {
  width: 100% !important;
  padding-left: 50px;
  padding-right: 50px;
}

.customText >>> .v-label {
  color: #13314a;
  font-size: 1.1em;
}

.customText >>> input {
  font-size: 1.2em;
}

.customText >>> .v-icon {
  color: #13314a;
}

.dis {
  padding-right:0px;
  padding-left: 0px;
}

.disLeft {
  padding-left: 40px !important;
  padding-right: 5px !important;
}

.disRight {
  padding-right: 40px !important;
  padding-left: 5px !important;
}
</style>

<script>
import { mapState, mapMutations } from "vuex";
import { validationMixin } from "vuelidate";
import {
  required,
  maxLength,
  minValue,
  minLength,
  email,
  alphaNum,
  decimal,
  requiredIf,
  numeric,
  integer,
} from "vuelidate/lib/validators";
const twoDecimals = (value) => (value * 100) % 1 == 0;
export default {
  name: "EditMovement",
  props: {
    id: Number,
    value: Boolean,
  },
  mixins: [validationMixin],
  validations: {
    movementConcept: { required, maxLength: maxLength(20) },
    movementAmount: {
      required,
      decimal,
      minAmount: function(value) {
        return value >= 0.01;
      },
      twoDecimals,
    },

    movementCategory: { required },

    movementType: { required },
    initialDate: { required },
  },
  computed: {
    show: {
      get() {
        return this.value;
      },
      set(value) {
        this.$emit("input", value);
      },
    },
    conceptErrors() {
      const errors = [];
      if (!this.$v.movementConcept.$dirty) return errors;
      !this.$v.movementConcept.maxLength &&
        errors.push("El concepto debe ocupar como máximo 20 caracteres.");
      !this.$v.movementConcept.required && errors.push("Campo requerido.");
      return errors;
    },
    amountErrors() {
      const errors = [];
      if (!this.$v.movementAmount.$dirty) return errors;
      !this.$v.movementAmount.decimal &&
        errors.push("El importe debe ser numérico.");
      !this.$v.movementAmount.required && errors.push("Campo requerido.");
      !this.$v.movementAmount.minAmount &&
        errors.push("El importe debe ser superior a 0€.");
      !this.$v.movementAmount.twoDecimals &&
        errors.push("El importe debe tener como máximo 2 decimales.");
      return errors;
    },

    categoryErrors() {
      const errors = [];
      if (!this.$v.movementCategory.$dirty) return errors;
      !this.$v.movementCategory.required && errors.push("Campo requerido.");
      return errors;
    },
    typeErrors() {
      const errors = [];
      if (!this.$v.movementType.$dirty) return errors;
      !this.$v.movementType.required && errors.push("Campo requerido.");
      return errors;
    },
    initialDateErrors() {
      const errors = [];
      if (!this.$v.initialDate.$dirty) return errors;
      !this.$v.initialDate.required && errors.push("Campo requerido.");
      return errors;
    },

    ...mapState(["auxMov"]),
  },
  mounted() {
    this.getById(this.id);
    this.movementType = this.auxMov.type;
    this.movementConcept = this.auxMov.concept;
    this.movementAmount = this.auxMov.amount;
    this.movementComment = this.auxMov.comment;
    this.movementCategory = this.auxMov.category;
    this.initialDate = this.auxMov.date;
  },
  watch: {
    id: function() {
      this.idMovement = this.id;
      this.getById(this.id);
      this.movementType = this.auxMov.type;
      this.movementConcept = this.auxMov.concept;
      this.movementAmount = this.auxMov.amount;
      this.movementComment = this.auxMov.comment;
      this.movementCategory = this.auxMov.category;
      this.initialDate = this.auxMov.date;
    },
  },
  data() {
    return {
      idMovement: 1,
      color1: "#445e73",
      color2: "#13314a",
      color3: "#445e73",
      color4: "#13314a",
      isUnique: true,
      pressedI: false,
      pressedG: true,
      categories: ["Ocio", "Servicios", "Compras", "Ingresos", "Otro"],
      initialDate: new Date().toISOString().substr(0, 10),
      menu2: false,
      menu1: false,
      movementConcept: "",
      movementComment: "",
      movementAmount: undefined,
      movementType: "Gasto",
      movementCategory: undefined,
      submitStatus: null,
    };
  },
  methods: {
    switchG() {
      this.color1 = "#445e73";
      this.color2 = "#13314a";
    },
    switchI() {
      this.color2 = "#445e73";
      this.color1 = "#13314a";
    },
    confirm() {
      this.$v.$touch();
      if (this.isInvalid()) {
        console.log("error");
        this.submitStatus = "ERROR";
      } else if (this.isUnique) {
        this.submitStatus = "PENDING";
        this.modUniqueMovement();
        this.show = false;
        setTimeout(() => {
          this.submitStatus = "OK";          
        }, 500);
      }
    },
    modUniqueMovement() {
      var movement = {
        id: this.idMovement,
        concept: this.movementConcept,
        amount: parseFloat(this.movementAmount),
        category: this.movementCategory,
        type: this.movementType,
        date: this.initialDate,
        comment: this.movementComment,
      };
      this.modifyUniqueMovement(movement);
      this.$emit("update");
    },
    cancel() {
      this.show = false;
    },
    isInvalid() {
      var inv =
        this.$v.movementConcept.$invalid ||
        this.$v.movementAmount.$invalid ||
        this.$v.movementCategory.$invalid ||
        this.$v.movementType.$invalid ||
        this.$v.initialDate.$invalid;
      return inv;
    },
    ...mapMutations(["getById", "modifyUniqueMovement"]),
  },
};
</script>
