<template>
  <v-data-table
    :headers="headers"
    :items="movs"
    sort-by="date"
    :sort-desc="true"
    class="elevation-15 table"
    multi-sort
    style="padding: 20px; border: 3px solid #13314a; border-radius: 20px"
    :footer-props="{
      'items-per-page-text': 'Movimientos por página',
    }"
  >
    <template v-slot:top>
      <v-toolbar flat class="toolbar">
        <v-spacer></v-spacer>
        <h2 class="title">HISTORIAL DE MOVIMIENTOS</h2>
        <v-spacer></v-spacer>
        <v-btn @click="generatePDF" large color="primary" class="mr-2">
          <span class="mr2">Descargar PDF</span>
          <v-icon> mdi-file-pdf </v-icon>
        </v-btn>
        <JsonCSV
          name="movimientos.csv"
          :labels="labels"
          :fields="fields"
          :data="movs"
        >
          <v-btn large color="primary"
            ><span class="mr-2">Descargar CSV</span
            ><v-icon>mdi-file-delimited</v-icon></v-btn
          >
        </JsonCSV>
        <EditMovement :id="editedItem.id" v-model="dialog"></EditMovement>
        <v-dialog v-model="dialogDelete" max-width="500px">
          <v-card>
            <v-card-title class="headline"
              >¿Estás seguro de que deseas borrar el movimiento?</v-card-title
            >
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="closeDelete"
                >Cancelar</v-btn
              >
              <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                >Confirmar</v-btn
              >
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>
    <template v-slot:[`item.actions`]="{ item }">
      <v-btn icon medium @click="editItem(item)" color="white" class="ic mr-2">
        <v-icon> mdi-pencil </v-icon>
      </v-btn>
      <v-btn icon medium @click="deleteItem(item)" color="white" class="ic">
        <v-icon> mdi-delete </v-icon>
      </v-btn>
    </template>
    <template v-slot:no-data>
      <h3>No hay movimientos</h3>
    </template>
  </v-data-table>
</template>

<style scoped>
.table,
.toolbar {
  background-color: #f2fcff !important;
}
.title {
  font-size: 2em !important;
  color: #13314a !important;
}

.table >>> th {
  font-size: 1.2em !important;
  color: #13314a !important;
}
.table >>> td {
  font-size: 1em !important;
}

.ic {
  background-color: #13314a;
}
</style>

<script>
import { mapState, mapMutations } from "vuex";
import jsPDF from "jspdf";
import EditMovement from "../components/EditMovement";
import JsonCSV from "vue-json-csv";
import VueHtml2pdf from "vue-html2pdf";
import JsPDFAutotable from 'jspdf-autotable' 
export default {
  name: "HistoryTable",
  components: { EditMovement, JsonCSV, VueHtml2pdf, jsPDF, JsPDFAutotable },
  props: {
    movs: {
      type: Array,
      required: true,
    },
  },
  data: () => ({
    fields: ["id", "type", "concept", "amount", "date", "category", "comment"],
    labels: {
      concept: "concepto",
      amount: "importe",
      category: "categoría",
      type: "tipo",
      date: "fecha",
      comment: "comentario",
    },
    dialog: false,
    dialogDelete: false,
    headers: [
      {
        text: "TIPO",
        align: "start",
        sortable: true,
        value: "type",
      },
      { text: "CONCEPTO", value: "concept", align: "start", sortable: true },
      { text: "IMPORTE (€)", align: "start", value: "amount" },
      { text: "CATEGORÍA", align: "start", value: "category" },
      { text: "FECHA", align: "start", value: "date" },
      { text: "ACCIONES", align: "center", value: "actions", sortable: false },
    ],
    desserts: [],
    editedIndex: -1,
    editedItem: { id: 1 },
    defaultItem: {
      name: "",
      calories: 0,
      fat: 0,
      carbs: 0,
      protein: 0,
    },
  }),

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "New Item" : "Edit Item";
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },

  methods: {
    editItem(item) {
      this.editedIndex = this.movs.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      this.editedIndex = this.movs.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      this.movs.splice(this.editedIndex, 1);
      this.removeUniqueMovement(this.editedItem.id);
      this.$emit("update");
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    save() {
      if (this.editedIndex > -1) {
        Object.assign(this.desserts[this.editedIndex], this.editedItem);
      } else {
        this.desserts.push(this.editedItem);
      }
      this.close();
    },
    generatePDF() {
      var vm = this;
      var columns = [
        { title: "Id", dataKey: "id" },
        { title: "Tipo", dataKey: "type" },
        { title: "Concepto", dataKey: "concept" },
        { title: "Importe", dataKey: "amount" },
        { title: "Fecha", dataKey: "date" },
        { title: "Categoría", dataKey: "category" },
        { title: "Comentario", dataKey: "comment" },
      ];
      var doc = new jsPDF("p", "pt");
      doc.text("Historial de Movimientos", 40, 40);
      console.log(vm.movs)
      doc.autoTable(columns, vm.movs, {
        margin: { top: 60 },
      });
      doc.save("Movimientos.pdf");
    },
    ...mapMutations(["removeUniqueMovement"]),
  },
};
</script>
