<template>
  <v-app :style="bg">
    <v-app-bar app color="primary" dark style="z-index:999">
      <div class="d-flex align-center">
        <v-img
          alt="Vuetify Logo"
          class="shrink mr-2"
          contain
          src="https://cdn.vuetifyjs.com/images/logos/vuetify-logo-dark.png"
          transition="scale-transition"
          width="40"
        />

        <v-img
          alt="Vuetify Name"
          class="shrink mt-1 hidden-sm-and-down"
          contain
          min-width="100"
          src="https://cdn.vuetifyjs.com/images/logos/vuetify-name-dark.png"
          width="100"
        />
      </div>
      <div v-if="logged" style="margin-left: 20px">
        <v-btn large class="text" text to="/"
          ><span class="mr-2">Inicio</span> <v-icon> mdi-home</v-icon></v-btn
        >
        <v-btn large class="text" text to="/crear"
          ><span class="mr-2">Añadir movimiento</span>
          <v-icon> mdi-pencil</v-icon></v-btn
        >
        <v-btn large class="text" text to="/graficos"
          ><span class="mr-2">Gráficos</span>
          <v-icon> mdi-chart-line</v-icon></v-btn
        >
        <v-btn large class="text" text to="/historial"
          ><span class="mr-2">Historial</span>
          <v-icon> mdi-folder-table</v-icon></v-btn
        >
        <v-btn large class="text" text to="/ayuda"
          ><span class="mr-2">Ayuda</span>
          <v-icon> mdi-help-circle</v-icon></v-btn
        >
      </div>

      <v-spacer></v-spacer>
      <v-btn large class="text" text to="/opciones"
        ><span class="mr-2">Opciones</span> <v-icon> mdi-cog</v-icon>
      </v-btn>
      <v-btn large class="text" text>
        <span class="mr-2" @click="exit">Cerrar sesión</span>
        <v-icon>mdi-login</v-icon>
      </v-btn>
    </v-app-bar>

    <v-main>
      <router-view />
    </v-main>
    <v-footer
      color="primary"
      dark
      class="py-4 text-center justify-center white--text"
      >2020 - Vuetify</v-footer
    >
  </v-app>
</template>

<style>
@import url("https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@700&display=swap");

#app {
  font-family: "Nunito Sans", sans-serif;
}
</style>

<style scoped>
.text {
  font-size: 1.1em;
}
</style>

<script>
import { mapState, mapMutations } from "vuex";

export default {
  name: "App",
  data: () => ({}),
  components: {},
  computed: {
    bg() {
      if (this.light) {
        return "background-color: #f2fcff";
      } else {
        return "background-color: #05111f";
      }
    },
    ...mapState(["logged", "username", "light"]),
  },
  methods: {
    exit() {
      this.logout();
      this.$router.push("/sesion");
    },
    ...mapMutations(["logout", "changeLight"]),
  },
};
</script>
