<template>
  <div id="balance">
    <div class="text-center" v-if="link">
      <h2
        style="cursor: pointer"
        class="title"
        @click="$router.push('/graficos')"
      >
        BALANCE
      </h2>
    </div>
    <div class="text-center" v-else>
      <h2
        class="title"
      >
        BALANCE
      </h2>
    </div>
    <apexchart
      type="area"
      height="310"
      :options="chartOptions"
      :series="series"
    ></apexchart>
  </div>
</template>

<style scoped>
.title {
  font-size: 1.65em !important;
  color: #13314a !important;
  font-weight: bold !important;
}
</style>

<script>
import VueApexCharts from "vue-apexcharts";
import { mapState, mapMutations } from "vuex";
export default {
  name: "Balance",
  props: {
    movements: {
      type: Array,
      required: true,
    },
    initialDate: String,
    finalDate: String,
    link: Boolean
  },
  mounted() {
    this.movements.sort(function (a, b) {
      var dateA = new Date(a.date);
      var dateB = new Date(b.date);
      if (dateA > dateB) {
        return 1;
      }
      if (dateA < dateB) {
        return -1;
      }
      return 0;
    });
  },
  computed: {
    dates() {
      var dates = [];
      var initial, final;

      if (
        this.initialDate === "" ||
        this.initialDate === undefined ||
        this.initialDate === null
      ) {
        initial = new Date(this.movements[0].date);
      } else if (
        new Date(this.initialDate) > new Date(this.movements[0].date)
      ) {
        initial = new Date(this.movements[0].date);
      } else {
        initial = new Date(this.initialDate);
      }
      if (
        this.finalDate === "" ||
        this.finalDate === undefined ||
        this.finalDate === null
      ) {
        final = new Date(this.movements[this.movements.length - 1].date);
      } else {
        final = new Date(this.finalDate);
      }
      for (
        initial;
        initial.getTime() <= final.getTime() + 1;
        initial.setDate(initial.getDate() + 1)
      ) {
        dates.push(new Date(initial).toISOString().substr(0, 10));
      }
      if (!dates.includes(final.toISOString().substr(0, 10))) {
        dates.push(final.toISOString().substr(0, 10));
      }
      return dates;
    },

    balance() {
      var totalDates = [...this.dates];
      if (totalDates.length === 0) {
        return [];
      }
      var balance = [];
      var accumulated = 0;

      var indexDates = 0;
      var indexMovements = 0;

      totalDates.forEach((date) => {
        balance.push(accumulated);
        let d = new Date(date);
        let dMov;
        if (indexMovements < this.movements.length) {
          dMov = new Date(this.movements[indexMovements].date);
        } else {
          dMov = new Date(this.movements[this.movements.length - 1].date);
        }
        while (
          indexMovements < this.movements.length &&
          d.getTime() === dMov.getTime()
        ) {
          let mov = this.movements[indexMovements];
          if (mov.type === "Ingreso") {
            balance[indexDates] += mov.amount;
            accumulated += mov.amount;
          } else {
            balance[indexDates] -= mov.amount;
            accumulated -= mov.amount;
          }
          indexMovements++;
          if (indexMovements < this.movements.length) {
            dMov = new Date(this.movements[indexMovements].date);
          }
        }
        indexDates++;
      });
      return balance;
    },

    chartOptions() {
      var chartOptions = {
        chart: {
          toolbar: {
             offsetY: 0,
          },
          height: 350,
          type: "area",
        },
        colors: ["#13314a"],
        dataLabels: {
          enabled: false,
        },
        stroke: {
          curve: "straight",
        },
        xaxis: {
          type: "datetime",
        },
        tooltip: {
          x: {
            format: "dd/MM/yy",
          },
        },
      };

      var initial, final;
      if (
        this.initialDate === "" ||
        this.initialDate === undefined ||
        this.initialDate === null
      ) {
        initial = new Date(this.movements[0].date);
      } else {
        initial = new Date(this.initialDate);
      }

      if (
        this.finalDate === "" ||
        this.finalDate === undefined ||
        this.finalDate === null
      ) {
        final = new Date(this.movements[this.movements.length - 1].date);
      } else {
        final = new Date(this.finalDate);
      }
      if (final < initial) {
        chartOptions.xaxis.categories = [];
        return chartOptions;
      }
      var posInitial, posFinal;
      posInitial = this.dates.indexOf(initial.toISOString().substr(0, 10));
      posFinal = this.dates.indexOf(final.toISOString().substr(0, 10));
      chartOptions.xaxis.categories = this.dates.slice(
        posInitial,
        posFinal + 1
      );

      return chartOptions;
    },
    series() {
      var series = [{ name: "Balance", data: [] }];
      if (
        (this.initialDate === "" ||
          this.initialDate === undefined ||
          this.initialDate === null) &&
        (this.finalDate === "" ||
          this.finalDate === undefined ||
          this.finalDate === null)
      ) {
        series[0].data = this.balance;
        return series;
      }
      var initial, final;

      if (
        this.initialDate === "" ||
        this.initialDate === undefined ||
        this.initialDate === null
      ) {
        initial = new Date(this.movements[0].date);
      } else {
        initial = new Date(this.initialDate);
      }

      if (
        this.finalDate === "" ||
        this.finalDate === undefined ||
        this.finalDate === null
      ) {
        final = new Date(this.movements[this.movements.length - 1].date);
      } else {
        final = new Date(this.finalDate);
      }
      if (final < initial) {
        return [];
      }
      var posInitial, posFinal;
      posInitial = this.dates.indexOf(initial.toISOString().substr(0, 10));
      posFinal = this.dates.indexOf(final.toISOString().substr(0, 10));

      series[0].data = this.balance.slice(posInitial, posFinal + 1);
      return series;
    },
    ...mapState(["uniqueMovements"]),
  },
  components: {
    apexchart: VueApexCharts,
  },
  data() {
    return {};
  },
  methods: {},
};
</script>
