<template>
  <div id="donut">
    <div class="text-center" v-if="link">
        <h2 style="cursor: pointer; " class="title" @click="$router.push('/graficos')">DISTRIBUCIÓN DE GASTOS</h2>
      </div>
      <div class="text-center" v-else>
        <h2 class="title">DISTRIBUCIÓN DE GASTOS</h2>
      </div>
    <apexchart
      type="donut"
      height="385"
      :options="chartOptions"
      :series="series"
    ></apexchart>
  </div>
</template>

<style scoped>
.title {
  font-size: 1.65em !important;
  color: #13314a !important;
  font-weight: bold !important;
}
</style>

<script>
import VueApexCharts from "vue-apexcharts";
import { mapState, mapMutations } from "vuex";
export default {
  name: "Donut",
  components: {
    apexchart: VueApexCharts,
  },
  props: {
    movements: {
      type: Array,
      required: true,
    },
    link: Boolean
  },
  created(){
    this.chartOptions.colors[0] = this.colOcio
    this.chartOptions.colors[1] = this.colServicios
    this.chartOptions.colors[2] = this.colCompras
    this.chartOptions.colors[3] = this.colOtro
  },
  computed: {
    series() {
      var series = [0, 0, 0, 0];
      if (this.movements != undefined && this.movements != null) {
        this.movements.forEach((element) => {
          if (element.category === "Ocio") {
            series[0] += element.amount;
          } else if (element.category === "Servicios") {
            series[1] += element.amount;
          } else if (element.category === "Compras") {
            series[2] += element.amount;
            // } else if (element.category === "Ingresos") {
            //   series[3] += element.amount;
          } else if (element.category === "Otro") {
            series[3] += element.amount;
          }
        });
      }
      return series;
    },
    ...mapState(["colOcio", "colServicios", "colCompras", "colOtro"]),
  },
  data() {
    return {
      chartOptions: {
        chart: {
          width: 1000,
          type: "donut",
          toolbar: {
            show:true,
          }
        },
        colors: ['#93C3EE', '#E5C6A0', '#669DB5', '#94A74A'],
        labels: ["Ocio", "Servicios", "Compras", "Otro"],
        legend: {          
          offsetY: 7,
          show: true,
          position: "bottom",
        },
        responsive: [
          {
            breakpoint: 480,
            options: {
              chart: {
                width: 800,
              },
              legend: {
                position: "bottom",
              },
            },
          },
        ],
      },
    };
  },
};
</script>
