<template>
  <div class="home">
    <div style="padding-right: 20px; padding-left: 20px; margin-bottom: 10px">
      <v-row justify="center" align="center">
        <v-col lg="6" md="12" class="d-flex" style="flex-direction: column">
          <div id="bal" ref="bal">
            <Balance
              :movements="aux"
              :link="true"
              class="elevation-15"
              style="
                padding: 20px;
                border: 3px solid #13314a;
                border-radius: 20px;
              "
            ></Balance>
          </div>
        </v-col>
        <v-col
          lg="3"
          md="6"
          
          style="margin-left: 0px; flex-direction: column"
        >
          <v-card
            class="elevation-15 text-center"
            height="443px"
            style="
              border: 3px solid #13314a;
              border-radius: 20px;
              background-color: #f2fcff !important;
              padding-top: 20px;
            "
          >
            <Donut :movements="aux" :link="true" class=""></Donut>
          </v-card>
        </v-col>
        <v-col
          lg="3"
          md="6"
          class="text-center d-flex"
          style="flex-direction: column"
        >
          <div >
            <v-card
              class="elevation-15"
              height="443px"
              style="
                cursor: pointer;
                border: 3px solid #13314a;
                border-radius: 20px;
                background-color: #f2fcff !important;
              "
              @click="$router.push('/crear')"
            >
              <h1 class="title mt-6">AÑADIR MOVIMIENTO</h1>
              <v-card-text style="font-size: 1.4em">
                ¿Deseas añadir un movimiento?<br /><br />
                Pulsa sobre esta tarjeta para hacerlo
              </v-card-text>
              <img
                class="mt-4 mb-5"
                width="80%"
                height="50%"
                src="../assets/undraw_settings_tab_mgiw.svg"
                alt=""
              />
            </v-card>
          </div>
        </v-col>
      </v-row>
    </div>
    <v-row>
      <v-col class="ml-4 mr-2">
        <SmallTable
          :movs="spent"
          title="ÚLTIMOS GASTOS"
          v-on:update="update"
          style="border: 3px solid #13314a; border-radius: 20px"
          class="elevation-15"
        ></SmallTable>
      </v-col>
      <v-col class="mr-4">
        <SmallTable
          :movs="incomes"
          title="ÚLTIMOS INGRESOS"
          v-on:update="update"
          style="border: 3px solid #13314a; border-radius: 20px"
          class="elevation-15"
        ></SmallTable>
      </v-col>
    </v-row>
    <v-btn
      style="font-size: 2em"
      fab
      dark
      large
      color="primary"
      fixed
      right
      bottom
      class="mb-14"
      @click="dialog = true"
      >?</v-btn
    >

    <Help title="INICIO" v-model="dialog" />

    <!-- <EditMovement :id="1"></EditMovement> -->
  </div>
</template>

<style scoped>
.home {
  margin: 20px;
}

.title {
  font-size: 1.7em !important;
  color: #13314a !important;
}
</style>

<script>
import VueAnime from "vue-animejs";
import HistoryTable from "../components/HistoryTable";
import SmallTable from "../components/SmallTable";
import EditMovement from "../components/EditMovement";
import Pie from "../components/Charts/Pie";
import Donut from "../components/Charts/Donut";
import Balance from "../components/Charts/Balance";
import Help from "../components/Help";
import { mapState, mapMutations } from "vuex";

export default {
  name: "Home",
  components: {
    HistoryTable,
    EditMovement,
    Pie,
    Donut,
    Balance,
    SmallTable,
    Help,
  },
  data: () => ({
    menu1: false,
    menu: false,
    dialog: false,
    concept: "",
    category: "",
    minAmount: undefined,
    maxAmount: undefined,
    type: "",
    initialDate: "",
    finalDate: "",
    filters: {
      concept: "",
      category: "",
      minAmount: 0,
      maxAmount: 0,
      type: "",
      initialDate: "",
      finalDate: "",
    },
    combos: ["Ocio", "Servicios", "Compras", "Ingresos", "Otro"],
    types: ["Ingreso", "Gasto"],
    headers: [
      {
        text: "Concept",
        value: "concept",
      },
      { text: "Amount", value: "amount" },
      { text: "Category", value: "category" },
      { text: "Type", value: "type" },
      { text: "Date", value: "date" },
    ],
    movements: [],
    aux: [],
    spent: [],
    incomes: [],
    filterSpent: { type: "Gasto" },
    filterIncomes: { type: "Ingreso" },
  }),
  created() {
    this.movements = [...this.uniqueMovements];
    this.aux = [...this.uniqueMovements];
    this.spent = this.filterSp();
    this.incomes = this.filterInc();
  },
  mounted() {
    //this.movements = [...this.uniqueMovements]
    //this.aux = [...this.uniqueMovements]
  },
  methods: {
    filterSp() {
      const cf = new this.$MultiFilters(this.movements, this.filterSpent);
      var items = this.movements;
      cf.registerFilter("type", function (searchWord, items) {
        if (searchWord.trim() === "") return items;
        return items.filter((item) => {
          return item.type.toLowerCase().includes(searchWord.toLowerCase());
        }, searchWord);
      });
      return cf.runFilters();
    },
    filterInc() {
      const cf = new this.$MultiFilters(this.movements, this.filterIncomes);
      var items = this.movements;
      cf.registerFilter("type", function (searchWord, items) {
        if (searchWord.trim() === "") return items;
        return items.filter((item) => {
          return item.type.toLowerCase().includes(searchWord.toLowerCase());
        }, searchWord);
      });
      return cf.runFilters();
    },
    update() {
      this.movements = [...this.uniqueMovements];
    },
    filter() {
      const cf = new this.$MultiFilters(this.movements, this.filters);
      var items = this.movements;
      cf.registerFilter("concept", function (searchWord, items) {
        if (searchWord.trim() === "") return items;
        return items.filter((item) => {
          return item.concept.toLowerCase().includes(searchWord.toLowerCase());
        }, searchWord);
      });
      cf.registerFilter("category", function (searchWords, items) {
        if (searchWords === null || searchWords.length === 0) return items;
        return items.filter((item) => {
          return searchWords.includes(item.category);
        }, searchWords);
      });
      cf.registerFilter("minAmount", function (minAmount, items) {
        if (minAmount === 0 || minAmount === null) return items;
        return items.filter((item) => {
          return item.amount >= minAmount;
        }, minAmount);
      });
      cf.registerFilter("maxAmount", function (maxAmount, items) {
        if (maxAmount === 0 || maxAmount === null) return items;
        return items.filter((item) => {
          return item.amount <= maxAmount;
        }, maxAmount);
      });
      cf.registerFilter("type", function (searchWord, items) {
        if (searchWord.trim() === "") return items;
        return items.filter((item) => {
          return item.type.toLowerCase().includes(searchWord.toLowerCase());
        }, searchWord);
      });
      cf.registerFilter("initialDate", function (initialDate, items) {
        if (initialDate === "" || initialDate === null) return items;
        var initialDateForm = new Date(initialDate);
        return items.filter((item) => {
          let itemDate = new Date(item.date);
          return itemDate >= initialDateForm;
        }, initialDate);
      });
      cf.registerFilter("finalDate", function (finalDate, items) {
        if (finalDate === "" || finalDate === null) return items;
        var finalDateForm = new Date(finalDate);
        return items.filter((item) => {
          let itemDate = new Date(item.date);
          return itemDate <= finalDateForm;
        }, finalDate);
      });
      this.aux = cf.runFilters();
    },
    filterConcept() {
      this.filters = this.$MultiFilters.updateFilters(this.filters, {
        concept: this.concept,
      });
    },
    filterCategory() {
      this.filters = this.$MultiFilters.updateFilters(this.filters, {
        category: this.category,
      });
    },
    filterMinAmount() {
      this.filters = this.$MultiFilters.updateFilters(this.filters, {
        minAmount: parseFloat(this.minAmount),
      });
    },
    filterMaxAmount() {
      this.filters = this.$MultiFilters.updateFilters(this.filters, {
        maxAmount: parseFloat(this.maxAmount),
      });
    },
    filterType() {
      this.filters = this.$MultiFilters.updateFilters(this.filters, {
        type: this.type,
      });
    },
    filterInitialDate() {
      this.menu1 = false;
      this.filters = this.$MultiFilters.updateFilters(this.filters, {
        initialDate: this.initialDate,
      });
    },
    filterFinalDate() {
      this.menu = false;
      this.filters = this.$MultiFilters.updateFilters(this.filters, {
        finalDate: this.finalDate,
      });
    },
    clear() {
      this.minAmount = undefined;
      this.maxAmount = undefined;
      this.concept = "";
      this.category = "";
      this.type = "";
      this.initialDate = "";
      this.finalDate = "";
      this.aux = this.movements;
      this.filters = {
        concept: "",
        category: "",
        minAmount: 0,
        maxAmount: 0,
        type: "",
        initialDate: "",
        finalDate: "",
      };
    },
  },
  computed: {
    ...mapState(["uniqueMovements"]),
  },
};
</script>
