import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import store from '../store/index'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Inicio',
    component: Home,
    meta: {
      requiresAuth: true
    }

  },
  {
    path: '/historial',
    name: 'Historial',
    component: () => import('../views/History.vue'),
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/graficos',
    name: 'Graficos',
    component: () => import('../views/Charts.vue'),
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/crear',
    name: 'Crear',
    component: () => import('../views/AddMovement.vue'),
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/sesion',
    name: 'Sesion',
    component: () => import( '../views/SignIn.vue'),
    meta: {
      hideForAuth: true
    }
  },
  {
    path: '/opciones',
    name: 'Settings',
    component: () => import( '../views/Settings.vue'),
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/ayuda',
    name: 'HelpCenter',
    component: () => import('../views/HelpCenter.vue'),
    meta: {
      requiresAuth: true
    }
  },
  { path: '/', redirect: '/sesion' },
  { path: '*', redirect: '/sesion' },
]

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    if (!store.state.logged) {
      next({ path: '/sesion' });
    } else {
      next();
    }

  } else {
    next();
  }

  if (to.matched.some(record => record.meta.hideForAuth)) {
    if (store.state.logged) {
      next({ path: '/' });
    } else {
      next();
    }
  } else {
    next();
  }
})

export default router 
