import Vue from "vue";
import Vuex from "vuex";
import createPersistedState from "vuex-persistedstate";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    logged: false,
    username: "Prueba",
    name: "Prueba",
    email: "prueba@prueba.com",
    password: "prueba",
    light: true,
    uniqueMovements: [],
    periodicMovements: [],
    auxMov: null,
    colOcio: "#9c27b0",
    colServicios: "#ff9800",
    colCompras: "#4caf50",
    colOtro: "#2196F3",
  },
  mutations: {
    login(state, username) {
      state.logged = true;
      state.username = username;
    },
    logout(state) {
      state.logged = false;
      state.username = "Prueba";
      state.name = "Prueba";
      state.email = "prueba@prueba.com";
      state.password = "prueba";
      state.uniqueMovements = [];
      state.periodicMovements = [];
      state.colOcio = "#9c27b0";
      state.colServicios = "#ff9800";
      state.colCompras = "#4caf50";
      state.colOtro = "#2196F3";
    },
    setEmail(state, email) {
      state.email = email;
    },
    setName(state, name) {
      state.name = name;
    },
    setUsername(state, username){
      state.username = username
    },
    setPassword(state, password){
      state.password = password
    },
    updateProfile(state, name, username, email, password) {
      state.name = name;
      state.username = username;
      state.email = email;
      state.password = password;
    },
    changeLight(state) {
      state.light = !state.light;
    },
    addUniqueMovement(state, movement) {
      if (state.uniqueMovements.length == 0) {
        movement.id = 1;
      } else {
        var lastID = state.uniqueMovements.slice(-1)[0].id;
        movement.id = lastID + 1;
      }
      state.uniqueMovements.push(movement);
    },
    addPeriodicMovement(state, movement) {
      if (state.periodicMovements.length == 0) {
        movement.id = 1;
      } else {
        var lastID = state.periodicMovements.slice(-1)[0].id;
        movement.id = lastID + 1;
      }
      state.periodicMovements.push(movement);
    },
    removeUniqueMovement(state, movementId) {
      for (let [index, movement] of state.uniqueMovements.entries()) {
        if (movement.id == movementId) {
          state.uniqueMovements.splice(index, 1);
        }
      }
    },
    removePeriodicMovement(state, movementId) {
      for (let [index, movement] of state.periodicMovements.entries()) {
        if (movement.id == movementId) {
          state.periodicMovements.splice(index, 1);
        }
      }
    },
    modifyUniqueMovement(state, modifier) {
      //console.log("movement")
      for (let [index, movement] of state.uniqueMovements.entries()) {
        if (movement.id == modifier.id) {
          movement.concept = modifier.concept;
          movement.amount = modifier.amount;
          movement.category = modifier.category;
          movement.type = modifier.type;
          movement.date = modifier.date;
          movement.comment = modifier.comment;
        }
      }
    },
    modifyPeriodicMovement(state, modifier) {
      for (let [index, movement] of state.periodicMovements.entries()) {
        if (movement.id == modifier.id) {
          movement.concept = modifier.concept;
          movement.amount = modifier.amount;
          movement.category = modifier.category;
          movement.type = modifier.type;
          movement.comment = modifier.comment;
          movement.initialDate = modifier.initialDate;
          movement.finalDate = modifier.finalDate;
          movement.freq = modifier.freq;
          movement.freqUnit = modifier.freqUnit;
        }
      }
    },
    getById(state, id) {
      for (let [index, movement] of state.uniqueMovements.entries()) {
        if (movement.id == id) {
          state.auxMov = movement;
        }
      }
    },
    setColOcio(state, color) {
      state.colOcio = color;
    },
    setColServicios(state, color) {
      state.colServicios = color;
    },
    setColCompras(state, color) {
      state.colCompras = color;
    },
    setColOtro(state, color) {
      state.colOtro = color;
    },
  },
  plugins: [createPersistedState()],
  actions: {},
  modules: {},
});
